@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@300;400;500;600;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-color: rgb(0, 0, 0);
  color: #fff;
  font-family: "Oswald", sans-serif;
  font-weight: 500;
  height: 100vh;
  width: 100%;
  position: relative;
}
a {
  text-decoration: none;
  color: #fff;
}

.app__container {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
}
@media screen and (min-width: 1200px) {
  body {
    background: url(/static/media/hero-bg2.55393ba3.jpg);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
  }
}

/* MAIN CONTAINER */
.navbar__container {
    height: 10vh;
    background-color: rgb(89, 55, 242);
    color: #000;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    padding: 1rem;
    font-size: 1.5rem;
    z-index: 10;
    box-shadow: 0px 5px 5px rgba(0, 0, 0, 0.337);
    display: flex;
    justify-content: center;
    align-items: center;
}
.nav__content--container {
    width: 100%;
    max-width: 1200px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.navbar__logo {
    text-transform: uppercase;
    font-size: 1.5rem;
    color: #000;
}
/* LINKS CONTAINER */
.navbar__links--container {
    display: flex;

}
/* SIDEBAR OPEN/CLOSE BUTTONS */
.sidebar__toggle--btn {
    display: none;
    position: absolute;
    right: 1rem;
    cursor: pointer;
    transition: 0.3s;
}
.sidebar__toggle--btn:hover {
    transform: scale(1.1);
}
/* RESPONSIVE NAVBAR */
@media screen and (max-width: 500px){
    .navbar__links--container {
        display: none;
    }
    .sidebar__toggle--btn {
        display: block;
    }
}
.navbar__link {
    font-size: 1.2rem;
    margin-right: 1rem;
    cursor: pointer;
    text-transform: uppercase;
    text-decoration: none;
    color: #000;
}
.navbar__link:hover, .navbar__link:active{
    transform: scale(1.1);
    color: #fff;
}
.hidden {
    display: none;
}
.hero__container {
  height: 100vh;
  background: url(/static/media/hero-bg2.55393ba3.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  display: flex;
  align-items: center;
  
}

.overlay {
  position: absolute;
  height: 100%;
  width: 100%;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.028),rgba(0, 0, 0, 0.268));
  z-index: 1;
}
@media screen and (min-width: 1200px) {
  .hero__container {
    background: none;
  }
  .overlay {
    position: absolute;
    height: 100%;
    width: 100%;
    background: none;
    z-index: 1;
  }
}
.hero__titles--container {
  z-index: 2;
  padding: 2rem;
  /* border: 1px solid #123; */
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.hero__main--title {
  font-size: 5rem;
  color: rgb(255, 255, 255);
  text-transform: uppercase;
  text-shadow: 3px 3px 10px rgba(0, 0, 0, 0.453);
}
.hero__sub--title {
  font-size: 3rem;
  color: rgb(255, 255, 255);
  text-transform: uppercase;
  text-align: left;
  text-shadow: 3px 3px 10px rgba(0, 0, 0, 0.453);
}

.hero__info--text {
  margin-top: 2rem;
  text-align: left;
  text-shadow: 3px 3px 10px rgba(0, 0, 0, 0.953);
  letter-spacing: 1px;
  font-size: 1rem;
  text-transform: uppercase;
}

.hero__learn-more-btn {
  margin-top: 1rem;
  font-size: 0.8rem;
  font-weight: 400;
  letter-spacing: 1px;
  text-decoration: none;
  cursor: pointer;
  
  border-radius: 20px;
  padding: 0.5rem 0.75rem;
  color: rgb(255, 255, 255);
  background-color: rgb(89, 55, 242);
}
.hero__learn-more-btn:hover {
  color: rgb(255, 255, 255);
  background-color: rgb(118, 0, 0);
  border-radius: 20px;
  padding: 0.5rem 0.75rem;
  color: rgb(58, 42, 42);
  background-color: rgb(255, 255, 255);
}

.sidebar__container {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 30;
  /* background-color: #000; */
  display: flex;
  flex-direction: column;
  border: 1px solid #fff;
  transform: translate(-100%);
  transition: 0.3s ease-in;
  background: linear-gradient(90deg,rgb(89, 55, 242), rgb(0, 0, 0),rgb(255, 255, 255) );
  background-repeat: no-repeat;
  background-size: 400% 300%;
}
@keyframes gradient {
  0% {
    background-position: 0% 75%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0% 50%;
  }
}
.sidebar__top--container {
  height: 10vh;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  font-size: 1.5rem;
}

.sidebar__logo {
  text-transform: uppercase;
  color: #000;
}

.logo-text-span {
  color:#fff;
}
.close-sidebar__btn {
  transition: 0.3s;
  cursor: pointer;
}
.close-sidebar__btn:hover {
  transform: scale(1.1);
}
.sidebar__links--container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  text-transform: uppercase;
  letter-spacing: 3px;
  padding: 2rem 0;
}
.sidebar--link {
  cursor: pointer;
  padding: 1rem 1rem;
  width: 100%;
  text-align: center;
}
.sidebar--link:hover {
  background-color: rgb(255, 255, 255);
  color: #000;
}
.show-sidebar {
  transform: translate(0);
  animation: gradient 0.5s ease-in;
}

.social__links--container {
  display: flex;
  /* flex-direction: column; */
  align-items: center;
  justify-content: center;
}
.social__link {
  margin: 0.5rem;
  color: rgb(0, 0, 0);
  font-size: 1.5rem;
}
.social__link:hover {
  color: rgb(255, 255, 255);
  transform: scale(1.1);
}

.about__page--container {
  min-height: 90vh;
  background: url(/static/media/hero-bg2.55393ba3.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  position: relative;
  z-index: 2;
}
@media screen and (max-width: 600px){
  .about__page--container {
    /* min-height: 90vh; */
    background: none;
  /* background: url(../assets/hero-bg-mobile.jpg); */

    background-position: left;
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment:local;
    position: relative;
    z-index: 2;
  }
}
.overlay-pages {
    position: absolute;
    min-height: 100%;
    width: 100%;
    top: 0;
    right: 0;
    background:linear-gradient(90deg,rgba(255, 255, 255, 0.186), rgba(0, 0, 0, 0.385));
    filter: blur(100px);
    z-index: -1;
    transition: 5s ease;
}

.tech__container{
    min-height: 100vh;
    /* background-color: rgba(0, 0, 0, 0.392); */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 1rem;
}
.tech__container--title {
    text-transform: uppercase;
    font-size: 3rem;
    font-weight: 400;
    margin-bottom: 1rem;
}
.all__tech--container {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(150px, 1fr));
}
@media screen and (min-width:500px){
    .all__tech--container {
        display: flex;
        grid-template-columns: none;
        overflow: scroll;
        border-radius: 10px;
        max-width: 800px;
    }
}
.tech__control, .trivia__control {
    padding: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items:center;
    background-color: rgba(0, 0, 0, 0.631);
}
.tech__name {
    font-size: 4rem;
}
.tech__label {
    font-size:1rem;

}
.bio__container {
  min-height: 100vh;
  display: grid;
  grid-template-columns: 1fr 9fr;
  grid-gap: 1rem;
  gap: 1rem;
  place-content: center;
  padding: 1rem;
}
/* SELECTED BIO CONTAINER */
.current__bio--option {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

/* BIO OPTION INIDCATOR */
.bio__options--container {
  height: 60vh;
  /* border: 2px solid #fff; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 0;
}
.bio__option {
  cursor: pointer;
  font-size: 1.5rem;
  margin: 0.5rem;
}
.bio__option:hover {
  transform: scale(1.1);
}
.bio__option.active {
  color: rgb(89, 55, 242);
  transform: scale(1.1);
  animation: expandAndDisappear 1s ease-in-out;
}
@keyframes expandAndDisappear {
  0% {
    transform: translateY(0) scale(1);
    opacity: 1;
  }
  100% {
    transform: translateY(20px) scale(1.2);
    opacity: 0;
  }
}
.options__span {
  background-color: rgb(255, 255, 255);
  border-radius: 5px;
  width: 4px;
  height: 100%;
  position: relative;
  cursor: pointer;
}
.options__span::after {
  display: block;
  content: "";
  position: absolute;
  top: 0px;
  left: 0px;
  background-color: rgb(89, 55, 242);
  border-radius: 5px;
  width: 4px;
  height: 0%;
}
.options__span.active.options__span::after {
  transition: 1s;
  height: 100%;
}

/* --------------------- */
/* -----BIO OPTIONS----- */
/* --------------------- */


/* BIO OPTION TYPEWRITER */
.type-text-wrapper {
  height: 100%;
  font-size: 2rem;
  text-transform: uppercase;
  color: #fff;
}
.type-text--cursor {
  font-size: 0.2rem;;
}
/* BIO OPTION FAKE STORY */
.bio__fake--story {
  font-weight: 300;
  background-color: rgb(0, 0, 0);
  padding: 1rem;
  border-radius: 10px;
  text-align: justify;
  text-transform: uppercase;
}

